.canvas-wrap-p {
  height:65%;
}

.canvas_root{
  height: 100%;
}

.canvas_tool{
  height: 5%;
  background-color:#f2f2f2;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content:start;
}


#rank-p{
  /*overflow: auto;*/
  margin: 0 auto;
  width: 100%;
  height: 25%;
  text-align:center;
}

.answer-p{
  height: 5%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content:center;
}


.color-picker{
  margin: 10px;
  padding: 0%;
  border-radius: 5px;
  background:rgb(255, 255, 255); 
}

input[type=range]{
  margin:auto;
  width: 50%;
  -webkit-appearance: none;
  cursor: pointer;
}

input[type=range]::-webkit-slider-thumb{
  -webkit-appearance: none;
  height:20px;
  width:15px;
  background:rgb(66, 164, 255);
  /*border-color: #000;*/
  border:none;
  /*border-radius:70%;*/
  margin-top:-5px;
}
input[type=range]::-webkit-slider-runnable-track{
  background:rgb(228, 228, 228);
  height:12px;
  border-radius: 70%;
}
.canvas-wrap {
    height:65%;
  }
  
.canvas_root{
  height: 100%;
}

.answer{
  height: 7%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content:center;
}


#rank_con{
  overflow: auto;
  margin: 0 auto;
  width: 100%;
  height: 28%;
  text-align:center;
}


.clock{
  position: absolute;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  color: rgb(126, 126, 126);
}
#rank{
    overflow: auto;
    width:50%;
    height: 90%;
    margin: 5px 5px;
    background-color: rgba(255, 255, 255, 0.349)
}

#message{
    overflow: auto;
    width:50%;
    height: 90%;
    margin: 5px 5px;
    background-color:rgba(255, 255, 255, 0.349)
}
#rank_div{
    width: 100%;
    height: 100%;
    display: flex;
}
.component-app {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 100%;
}

#start_img{
    height: 50%;
    width: 80%;
    margin:40% 20% 5% 10% ;    
}

#start{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:space-between;
}

#name-input{
    margin: 10% auto;
}
